<template>
    <div class="canteen-list">        
        <div><Input search enter-button placeholder="搜索商家" size='large' class='searchInput' @on-search='selectButton' /></div> 
        <template v-for="canteen in canteenList">
       <Poptip trigger="hover"  placement='right' :key="canteen.canteenId"  word-wrap width="60">
            <Card class='card' @click.native="goInCanteen(canteen.canteenId)">
                <div slot="title"><p>{{canteen.canteenName}}</p><br><Rate disabled :value='canteen.canteenMark' allow-half/></div>
                <img :src='canteen.canteenImage'>
                <br>
                <p>销量:{{canteen.sales}}</p>               
            </Card>
            <div class="api" slot="content">
             <Card>
                <div slot="title"><p>{{canteen.canteenName}}</p><br><b>{{canteen.canteenLabel}}</b></div>
                <p>{{canteen.canteenDes}}</p>              
            </Card>          
        </div>
    </Poptip>
    </template>    
    </div>
</template>
<script>
import {getCanteenObj,selectByName} from '@/api/api.js'
export default {
    data(){
        return{
            canteenList:[],       
        }
    },
    mounted(){
        let data={}
        getCanteenObj(data).then(res=>{     
            let list=res.data.data
            this.canteenList=list
        },err=>{

        })
    },
    methods:{
        goInCanteen(canteenId){
            this.$router.push({name:'clientFoodList',query:{canteenId:canteenId}})
        },
        selectButton(val){
            //用户没输入就搜索
            if(!val){
                this.$Message.error('亲，请输入搜索内容')
                return
            }
            let data={canteenName:val}
            selectByName(data).then(res=>{
                this.canteenList=res.data.data
            },err=>{
                this.$Message.error('网络出错')
            })
        }
    }
}
</script>
<style lang="less" scoped>
.canteen-list{
    background-color:#E8E8E8;
    img{
    width:80px;
    height:80px;
}
.card{
    margin:50px 0px 50px 140px;
    cursor: pointer;
}
b{
    font-size:10px
}
.searchInput{
    width:300px !important;
    margin:20px 0px 20px 1100px;
}  
}
</style>

